import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Alert } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			popout: null,
		};
		this.closePopout = this.closePopout.bind(this);
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data, activePanel: "home"});
					break;
				case 'VKWebAppJoinGroupResult':
					this.openPopout()
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	check_group = () => {
		var x = connect.send("VKWebAppCallAPIMethod", {"method": "groups.isMember", "params": {"group_id": 173243972, "user_id": this.state.fetchedUser.id, "v":"5.95", "access_token":"79d23ccd6697b3fd1b4bf95bba7ac60c47a9b96610e6d7bc5949a3e87cd7da21caca864a46afd1ef7a5d9"}}); 
		console.log(x);
		console.log(x.member)
		if(!x.member) { 
			return true;
		}
		else {
			return false;
		}
	}

	share = () => {
	console.log('1');
	console.log(this.check_group);
	if(this.check_group) { 
		connect.send("VKWebAppJoinGroup", {"group_id": 173243972}); }
	else { console.log(this.check_group); }
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};


	openPopout = () => {
		this.setState({ popout:
		  <Alert
			actions={[{
			  title: 'Ок',
			  autoclose: true,
			  style: 'destructive'
			}]}
			onClose={this.closePopout}>
			<p>Вы успешно подписались на сообщество. Спасибо за подписку :)</p>
		  </Alert>
		});
	  }
	   
	closePopout = () => {
		this.setState({ popout: null });
	}

	render() {
		return (
			<View popout={this.state.popout} activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} share={this.share}/>
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
